<template>
 <div class="footer">
     <ul class="footer-txt">
         <li class="footer-index active" ><router-link to="/index"><i></i>首页</router-link></li>
         <li class="footer-lend "><router-link to="/lend"><i></i>出借</router-link></li>
         <li class="footer-interact"><router-link to="/interact"><i></i>互动</router-link></li>
         <li class="footer-myCenter" @click="handelClick"><i></i>我的</li>
     </ul>
 </div>
</template>
<script>
import v from '../assets/js/jquery';
export default {
 template:"",
 data:function(){
     return { 
      }
 },
 methods:{
     handelClick(){
        //  console.log(global.sessionStorage.getItem("token"))
        if(global.sessionStorage.getItem("token")){
            // $(this.$vnode.elm).find(".footer-myCenter").addClass('active').siblings('li').removeClass('active');
            this.$router.push("/mycenter")
        }else {
             this.$router.push("/login")
        }
     }
 },
 mounted:function(){
    var pathName = location.href.split('/')[location.href.split('/').length-1];
    //console.log(pathName);
    switch (pathName){
        case 'index':
            $('.footer-index').addClass('active').siblings('li').removeClass('active');
            break;
        case 'lend':
            $('.footer-lend').addClass('active').siblings('li').removeClass('active');
            break;
        case 'interact':
            $('.footer-interact').addClass('active').siblings('li').removeClass('active');
            break;
        case 'mycenter':
            $('.footer-myCenter').addClass('active').siblings('li').removeClass('active');
            break;
    }
 }
}
</script>
