<template>
    <div class="myLogin">
        <div class="myLogin-title">
            <h1 class="icon-logo" style="position:relative;padding-left:3.5rem;box-sizing:border-box"></h1>
            <h1 class="login-txt l_orangeColor">欢迎登录</h1>
        </div>
        <div class="myLogin-section">
            <ul>
                <li class="inputTxt">
                    <input id="userName" type="text" placeholder="请输入手机号/用户名" class="l_orangeColor" maxlength="20" v-model="userName"  @focus="handleFocus"/>
                    <p class="l_close l_gay9">+</p>
                    <h5 class="l_errorTip l_orangeColor">用户名不能为空</h5>
                </li>
                <li class="inputUpwd">
                    <input id="passWord" type="password" placeholder="请输入8-20位英文+数字密码" class="l_orangeColor" maxlength="20" v-model="passWord"  @focus="handleFocus"/>
                     <p class="l_pswBtn  l_gayColor"></p>
                    <h5 class="l_errorTip l_orangeColor">密码不能为空</h5>
                </li>
                <li class="forgetPwd"><a href="#" class="l_gay9">忘记密码 ?</a></li>
            </ul>
            <button class="l_btn" @click="doLogin">立即登录</button>
            <router-link to="register"  class="register l_gay9">注册领豪礼 ></router-link>
        </div>
    </div>
</template>
<style scoped>
    /*字体样式在base.css中*/
    .myLogin{
        width:100%;
        height:auto;
    }
    /*登录-logo*/
    .myLogin-title{
        width:90%;
        height:9rem;
        padding:5rem 5% 0;
    }
    .myLogin-title h1{
        width:100%;
        font-size:2.42rem;
        height:4rem;
        line-height:4rem;
        font-weight:400;
    }
    .icon-logo:before{
        content:"";
        position:absolute;
        top:0;
        left:-.6rem;
        width:4rem;
        height:4rem;
        background:url("../assets/images/login/icon_logo_orange.png") no-repeat;
        background-size:contain;
    }
    /*登录框*/
    button,
    input{
        outline:none;
        border-radius:0;
    }
    /*改变input中placeholder的颜色*/
    /* ::-weblit-input-placeholder{
        color:#f00;
    } */
    input::-webkit-input-placeholder{
        color:#eee;
    }
    .myLogin-section{
        width:90%;
        padding:2rem 5%;
        height:auto;
    }
    .myLogin-section ul{
        width:100%;
        height:auto;
    }
    .myLogin-section ul li.inputTxt,
    .myLogin-section ul li.inputUpwd{
        width:100%;
        height:3rem;
        line-height:3rem;
        margin-bottom:2rem;
        border-bottom:2px solid #f1f1f1;
        position:relative;
    }
    .myLogin-section ul li input{
        width:80%;
        height:1.6rem;
        line-height:1.6rem;
        padding:.7rem 0;
        border:0;
        font-size:1.6rem;
        letter-spacing:.2.8rem;
    }
    /*用户名和密码框后面的小××和小图标*/
    .l_close,
    .l_pswBtn{
        position:absolute;
        display:none;
    }
    .l_close{
        width:3rem;
        height:3rem;
        line-height:3rem;
        text-align:center;
        font-size:2rem;
        top:0;
        right:3%;
        transform:rotate(45deg);
    }
    /*睁眼和闭眼*/
    .l_pswBtn{
        width:2rem;
        height:2rem;
        right:5%;
        top:.5rem;
        background:url("../assets/images/login/register_icon01.png") 100% no-repeat;
        background-size:4rem;
    }
    .l_pswBtn1{
        background:url("../assets/images/login/register_icon01.png") 0 no-repeat;
        background-size:4rem;
    }
    /*验证提示信息h5标签*/
    .l_errorTip{
        position:absolute;
        width:100%;
        height:2rem;
        line-height:2rem;
        top:3rem;
        left:0;
        display:none;
    }
    /*忘记密码*/
    .forgetPwd{
        float:right;
        clear:both;
    }
    .l_btn{
        border:none;
        width:90%;
        height:4rem;
        border-radius:4rem;
        background:linear-gradient(to bottom,#ff5e46,#fea244);
        margin:4rem 5% 2rem;
        font-size:1.25rem;
        color:#fff;
        box-shadow:0 .2rem .4rem .1rem rgba(254,155,97,.6);
    }
    .register{
        display:block;
        width:8rem;
        height:2rem;
        line-height:2rem;
        margin:1rem auto 0;
        text-align:center;
    }
  
</style>
<script>
    import axios from "axios"
    export default {
        data: function () {
            return {
               userName:"",
               passWord:""
            }
        },
        components: {
        },
        methods: {
            doLogin:function(){
                if(this.userName==""){
                    $("#userName").siblings(".l_errorTip").show();
                }else if(this.passWord==""){
                    $("#passWord").siblings(".l_errorTip").show();
                }else {
                    //验证用户名和密码，发送异步请求，（需调接口）
                    //token时后台发送回来的数据用于存储用户信息，根据用户信息，来查询数据库进而显示不同用户看到的不同内容
                    //判断用户的登录状态是否过期，如果yoghurt已在其他地方登录，信息过期后，许重新调至登录界面
                    global.sessionStorage.setItem("token",1);
                    // console.log()
                    this.$router.push("index")
                }
            },
            handleFocus:function(){
                 $("#userName").siblings(".l_errorTip").hide();
                  $("#passWord").siblings(".l_errorTip").hide();
            }
        },
        computed: {},
        watch: {},
        mounted: function () {
            $(function(){
                //输入框获取焦点，下一个兄弟元素显示
                $(".myLogin-section").on("focus","input",function(){
                   $(this).next().show();
                   //输入框失去焦点，下一个兄弟元素隐藏
                }).on("blur","input",function(){
                   $(this).next().hide();
                }).on("touchend",".l_pswBtn",function(e){
                    //点击关闭的小鱼眼睛，切换成睁开的眼睛
                    e.preventDefault();
                    $(this).toggleClass("l_pswBtn1")
                    if($(this).hasClass("l_pswBtn1")){
                         $(this).prev().attr("type","text")
                    }else{
                        $(this).prev().attr("type","password")
                    }   
                    // 点击小叉叉，清空输入框的值
                }).on("touchend",".l_close",function(e){
                    e.preventDefault();
                    $(this).prev("#userName").val("");
                }).on("input","#userName",function(){
                    //输入框中的值大于20位时，多于20位的值将不显示
                    var reg=/^[a-zA-Z0-9]{6,20}$/;
                    var t=$(this);
                    if(!reg.test(t.val())){
                        if(t.val().length>=20){ 
                            t.val(t.val().slice(0,20))
                        }
                    }
                })
            })
        }
    }

</script>