<script>
    // import StepTime from "../components/Step.vue"
    import registerFormCheck from "../assets/js/formCheck.js"
    export default {
        data() {
           return {}
        },
        components: {
            // StepTime
        },
        methods: {
            ImgcodeSend(el) {
                //         var xmlhttp;
                //         xmlhttp=new XMLHttpRequest();
                //         xmlhttp.open("GET","/http://127.0.0.1/iqianbang/data/03_code_gg.php",true);
                //         xmlhttp.responseType = "blob";
                //         xmlhttp.onload = function(){
                //             if (this.status == 200) {
                //                 var blob = this.response;
                //                 el.setAttribute('src',window.URL.createObjectURL(blob));
                //                 window.URL.revokeObjectURL(window.URL.createObjectURL(blob));
                //             }
                //     }
                // xmlhttp.send();
                function dataURItoBlob(base64Data) {
                    var byteString;
                    if(base64Data.split(',')[0].indexOf('base64') >= 0)
                        byteString = atob(base64Data.split(',')[1]);
                    else
                        byteString = unescape(base64Data.split(',')[1]);
                    var mimeString = base64Data.split(',')[0].split(':')[1].split(';')[0];
                    var ia = new Uint8Array(byteString.length);
                    for(var i = 0; i < byteString.length; i++) {
                        ia[i] = byteString.charCodeAt(i);
                    }
                    return new Blob([ia], {
                        type: mimeString
                    });
                }
                //图片的base64
                var imgData="data:image/jped;base64,";
                el.setAttribute('src',window.URL.createObjectURL(dataURItoBlob(imgData)));
                window.URL.revokeObjectURL(window.URL.createObjectURL(dataURItoBlob(imgData)));   
            }         
        },
        computed: {},
        watch: {},
        mounted: function () {
           this.ImgcodeSend(document.getElementById("codeImg"));
            var stepP=document.querySelector(".myLogin>.l_timeLine");
            var clsName = stepP.getAttribute('class') + ' l_step';
            setTimeout(function(){stepP.setAttribute('class',clsName);},0);
            $(function(){
                //输入框获取焦点，下一个兄弟元素显示
                $(".myLogin-section").on("focus","input",function(){
                   $(this).next().show();
                   //输入框失去焦点，下一个兄弟元素隐藏
                }).on("blur","input",function(){
                   $(this).next().hide();
                //    console.log($(this).val()!=="")
                //    if($(this).val()!==""){$("l_btn").removeClass("l_btnDis")}
                }).on("touchend",".l_close",function(e){
                     // 点击小叉叉，清空输入框的值
                    e.preventDefault();
                    $(this).prev("#userName").val("");
                }).on("input","#userName #tel",function(){
                    //输入框中的值大于20位时，多于20位的值将不显示
                    var regTxt=/^[a-zA-Z0-9]{6,20}$/;
                    var regPwd=/^[a-zA-Z0-9]{8,20}$/
                     var t=$(this);
                    if(t==$("#userName")){                      
                        if(!regTxt.test(t.val())){
                            if(t.val().length>=20){ 
                                t.val(t.val().slice(0,20))
                            }
                        }
                    }
                })//还需要添加事件，当录入结束后，下一步改变颜色
            })
        },      
    }
</script>