        computed: {},
        watch: {},
        mounted: function () {
            $(function(){
                //轮播图
                var $ulImg=$(".carousel");
                var LIWIDTH=100;
                var timer=null,interval=500,wait=3000,moved=0;
                var number=$(".carousel li").size();
                $ulImg.css("width",LIWIDTH*(number)+"%");
                function move(){
                    moved++;
                    $ulImg.animate({left:-(moved*LIWIDTH)+"%"},interval,function(){
                        if(moved==number-1){
                            moved=0;
                            $ulImg.css("left",0);
                        }			
                    })
                }
                function autoMove(){
                    timer=setInterval(function(){
                        move();
                    },wait+interval)
                }
                autoMove();
                /**鼠标移入暂停**/
               $(".carousel li").hover(
                    function(){
                        clearInterval(timer);
                        timer=null;
                    },
                    function(){autoMove();}
                );
            })
            if(global.sessionStorage.getItem("token")){
                $(".infor-txt>.txt").children().first().addClass("unxSign").find("p").text("签到")
            }
        }
    }
</script>
