import Vue from 'vue'
import Router from 'vue-router'
import Main from './views/Main.vue'
import Index from './views/Index.vue'
import Lend from './views/Lend.vue'
import LendPerson from './views/LendPerson.vue'
import LendPlan from './views/LendPlan.vue'
import Login from './views/Login.vue'
import Register from './views/register.vue'
import History from './views/History.vue'
import Interaction from './views/Interaction.vue'
import MyCenter from './views/MyCenter.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {path: '/',component: Main,children:[
            {path:"",component:Index},
            {path:"index",component:Index},
            {path:"lend",component:Lend,children:[
              {path:"",component:LendPlan},
              {path:"lendplan",component:LendPlan},
              {path:"lendperson",component:LendPerson}
            ]
          },
          {path:"login",component:Login},
          {path:"register",component:Register},
          {path:"history",component:History},
          {path:"interact",component:Interaction},
          {path:"mycenter",component:MyCenter}
        ]
    }
  ]
})